:root {
  --vivid-auburn: rgb(145,31,31);
  --brown-chocolate: rgb(77,14,51);
  --yankees-blue: rgb(39,37,66);
  --old-lace: rgb(249,246,232);
  --columbia-blue: rgb(183,232,213);
  --pearl-aqua: rgb(130,200,193);
}

.gradient-text {
    background-color: #f3ec78;
    background-image: linear-gradient(#f3ec78, #af4261);
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

body {
  font-family: 'Permanent Marker', cursive;
  /* background-image: linear-gradient(rgb(100,20,44),var(--brown-chocolate),  var(--vivid-auburn), var(--pearl-aqua), var(--columbia-blue), var(--old-lace)); */
  background-color: rgb(39,37,66);
  background-color: var(--yankees-blue);
  /* margin-bottom: -2rem; */
}

img, svg {
  scale: 200%;
  /* color: var(--columbia-blue); */
}

.nav-items {
  background-color: rgb(39,37,66);
  background-color: var(--yankees-blue);
  /* background-image: linear-gradient(var(--old-lace), var(--columbia-blue), var(--pearl-aqua), var(--vivid-auburn), var(--brown-chocolate), var(--yankees-blue)); */
  color: rgb(183,232,213);
  color: var(--columbia-blue);
}

.navbar-brand {
  color: rgb(183,232,213);
  color: var(--columbia-blue);
}

.nav-link {
  color: rgb(183,232,213);
  color: var(--columbia-blue);
}

.nav-link.active {
  color: rgb(130,200,193);
  color: var(--pearl-aqua);
}

.navbar-nav .nav-link.active, .navbar-nav .show>.nav-link {
    color: rgb(130,200,193);
    color: var(--pearl-aqua);
}

.page-container {
  height: 50rem;
}

.footer{
  margin-top: 1rem;
  padding: 1rem;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: rgb(39,37,66);
  background-color: var(--yankees-blue);
  color: rgb(183,232,213);
  color: var(--columbia-blue);
}

.nav-container {
  /* margin-bottom: 5rem; */
  /* background-color: var(--yankees-blue); */
  background-image: linear-gradient(rgb(249,246,232), rgb(183,232,213), rgb(130,200,193), rgb(145,31,31), rgb(77,14,51), rgb(39,37,66));
  background-image: linear-gradient(var(--old-lace), var(--columbia-blue), var(--pearl-aqua), var(--vivid-auburn), var(--brown-chocolate), var(--yankees-blue));
  /* background-image: linear-gradient(var(--yankees-blue), var(--brown-chocolate)); */
  color: rgb(183,232,213);
  color: var(--columbia-blue);
}

.nav-ham {
    display: none;
}

@media (max-width: 768px) {

  .nav-ham {
    display: block;
    margin: 0;
    padding: 0;
    color: white;
  }

  .nav-items {
    display: none;
  }

}
:root {
  --vivid-auburn: rgb(145,31,31);
  --brown-chocolate: rgb(77,14,51);
  --yankees-blue: rgb(39,37,66);
  --old-lace: rgb(249,246,232);
  --columbia-blue: rgb(183,232,213);
  --pearl-aqua: rgb(130,200,193);
}

@-webkit-keyframes neon-box-shadow {
	0% {box-shadow:
        -1px -1px 0 rgb(183,232,213),
        1px -1px 0 rgb(183,232,213),
        -1px 1px 0 rgb(183,232,213),
        1px 1px 0 rgb(183,232,213),
        0 0 15px rgb(130,200,193);box-shadow:
        -1px -1px 0 var(--columbia-blue),
        1px -1px 0 var(--columbia-blue),
        -1px 1px 0 var(--columbia-blue),
        1px 1px 0 var(--columbia-blue),
        0 0 15px var(--pearl-aqua);}
	50% {box-shadow:
        -1px -1px 0 rgb(183,232,213),
        1px -1px 0 rgb(183,232,213),
        -1px 1px 0 rgb(183,232,213),
        1px 1px 0 rgb(183,232,213),
        0 0 55px rgb(130,200,193);box-shadow:
        -1px -1px 0 var(--columbia-blue),
        1px -1px 0 var(--columbia-blue),
        -1px 1px 0 var(--columbia-blue),
        1px 1px 0 var(--columbia-blue),
        0 0 55px var(--pearl-aqua);}
	100% {box-shadow:
        -1px -1px 0 rgb(183,232,213),
        1px -1px 0 rgb(183,232,213),
        -1px 1px 0 rgb(183,232,213),
        1px 1px 0 rgb(183,232,213),
        0 0 15px rgb(130,200,193);box-shadow:
        -1px -1px 0 var(--columbia-blue),
        1px -1px 0 var(--columbia-blue),
        -1px 1px 0 var(--columbia-blue),
        1px 1px 0 var(--columbia-blue),
        0 0 15px var(--pearl-aqua);}
}

@keyframes neon-box-shadow {
	0% {box-shadow:
        -1px -1px 0 rgb(183,232,213),
        1px -1px 0 rgb(183,232,213),
        -1px 1px 0 rgb(183,232,213),
        1px 1px 0 rgb(183,232,213),
        0 0 15px rgb(130,200,193);box-shadow:
        -1px -1px 0 var(--columbia-blue),
        1px -1px 0 var(--columbia-blue),
        -1px 1px 0 var(--columbia-blue),
        1px 1px 0 var(--columbia-blue),
        0 0 15px var(--pearl-aqua);}
	50% {box-shadow:
        -1px -1px 0 rgb(183,232,213),
        1px -1px 0 rgb(183,232,213),
        -1px 1px 0 rgb(183,232,213),
        1px 1px 0 rgb(183,232,213),
        0 0 55px rgb(130,200,193);box-shadow:
        -1px -1px 0 var(--columbia-blue),
        1px -1px 0 var(--columbia-blue),
        -1px 1px 0 var(--columbia-blue),
        1px 1px 0 var(--columbia-blue),
        0 0 55px var(--pearl-aqua);}
	100% {box-shadow:
        -1px -1px 0 rgb(183,232,213),
        1px -1px 0 rgb(183,232,213),
        -1px 1px 0 rgb(183,232,213),
        1px 1px 0 rgb(183,232,213),
        0 0 15px rgb(130,200,193);box-shadow:
        -1px -1px 0 var(--columbia-blue),
        1px -1px 0 var(--columbia-blue),
        -1px 1px 0 var(--columbia-blue),
        1px 1px 0 var(--columbia-blue),
        0 0 15px var(--pearl-aqua);}
}

.gradient-text {
    background-color: rgb(145,31,31);
    background-color: var(--vivid-auburn);
    background-image: linear-gradient(rgb(145,31,31), rgb(77,14,51), rgb(39,37,66));
    background-image: linear-gradient(var(--vivid-auburn), var(--brown-chocolate), var(--yankees-blue));
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
    /* animation: neon-text-shadow 3s ease-in-out infinite; */
}

h2 {
    margin-bottom: 2rem;
}

.page-container {
    margin-top: 10rem;
    margin-left: 15%;
}

.about {
    margin: 1rem;
}

.aboutIcon {
    margin: 1rem;
}

.aboutText {
    margin: 1rem;
    color: rgb(77,14,51);
    color: var(--brown-chocolate);
    font-size: larger;
}

.contact-btn {
    background-color: rgb(130,200,193);
    background-color: var(--pearl-aqua);
    color: rgb(39,37,66);
    color: var(--yankees-blue);
}

.contact-container {
    margin: 5rem;
    display: flex;
    justify-content: center;
}

.dark-txt {
    color: rgb(77,14,51);
    color: var(--brown-chocolate);
}

.download-btn {
    background-color: rgb(39,37,66);
    background-color: var(--yankees-blue);
    color: rgb(130,200,193);
    color: var(--pearl-aqua);
}

.download-btn:hover {
    background-color: rgb(145,31,31);
    background-color: var(--vivid-auburn);
    color: rgb(249,246,232);
    color: var(--old-lace);
}

.thanks {
    display: block;
    margin-left: 45%;
    margin-top: 10%;
}

.form-container {
    display: flex;
    align-items: center;
    margin-top: 3rem;
    padding: 3rem;
    text-align: center;
    background-image: linear-gradient(rgb(130,200,193), rgb(183,232,213), rgb(249,246,232), rgb(39,37,66), rgb(77,14,51), rgb(145,31,31));
    background-image: linear-gradient(var(--pearl-aqua), var(--columbia-blue), var(--old-lace), var(--yankees-blue), var(--brown-chocolate), var(--vivid-auburn));
    -webkit-animation: neon-box-shadow 3s ease-in-out infinite;
            animation: neon-box-shadow 3s ease-in-out infinite;
}

#contact-title {
    color: rgb(145,31,31);
    color: var(--vivid-auburn);
}

.icon-container {
    margin: 1rem;
}

 @media (max-width: 768px) {

    .page-container {
        margin: 2rem;
    }

    .contact-container {
        margin: 0;
    }

    .thanks {
        margin-left: 35%;
    
    }

